type MerkleNode = {
  nodeId: number;
  digest: string;
  address: string;
  amount: string;
  startTime: number;
  endTime: number;
};

export class SyncVestingProofSDK {
  constructor(private data: MerkleNode[]) {}

  merkleProofFromJson(address: string): Array<string> {
    const data = this.data;

    let leaf: MerkleNode | null = null;
    let leaves = data.filter((node) => {
      return String(node.address).toLowerCase() === String(address).toLowerCase();
    });

    if (leaves.length === 1) {
      leaf = leaves[0];
    }

    if (leaf === null) {
      throw new Error('address not found');
    }

    let proof: Array<string> = [];

    let siblingId;
    if (leaf.nodeId % 2 === 0) {
      siblingId = leaf.nodeId + 1;
    } else {
      siblingId = leaf.nodeId - 1;
    }
    let sibling = this.getByNodeId(siblingId);
    if (sibling == null) {
      throw new Error('sibling null');
    }
    proof.push(sibling.digest);

    let currentNode = leaf;
    while (Math.floor(currentNode.nodeId / 2) !== 1) {
      let fatherId = Math.floor(currentNode.nodeId / 2);

      let father = this.getByNodeId(fatherId);
      if (father == null) {
        throw new Error('father null');
      }
      let siblingId;
      if (father.nodeId % 2 === 0) {
        siblingId = father.nodeId + 1;
      } else {
        siblingId = father.nodeId - 1;
      }
      let sibling = this.getByNodeId(siblingId);
      if (sibling == null) {
        throw new Error('sibling null');
      }

      proof.push(sibling.digest);

      currentNode = father;
    }

    //push root at least
    let root = this.getByNodeId(1);
    if (root == null) {
      throw new Error('root null');
    }
    proof.push(root.digest);

    return proof;
  }

  getByNodeId(nodeId: number): MerkleNode | undefined {
    let temp = this.data.filter((node) => node.nodeId === nodeId);

    if (temp.length === 1) return temp[0];
  }

  getByAddress(address: string): MerkleNode | undefined {
    return this.data.find((node) => node.address.toLowerCase() === address.toLowerCase());
  }
}
