import { AirDropMerkleProof__factory } from '../contracts';
import { useEthers } from '@usedapp/core';
import { BigNumber } from 'ethers';
import { SyncVestingProofSDK } from './helper';
import { MERKLE_PROOFS } from './airdrop';
import { QueryObserverResult, useQuery } from 'react-query';
import { AIRDROP_CONTRACT_ADDRESS, AIRDROP_TOKEN_SYMBOL } from 'config';

interface ClaimVesting {
  claimable: boolean;
  claimableAmount: BigNumber;
  claimedAmount: BigNumber;
  tokenSymbol: string;
  claim: () => Promise<string>;
  startTime: number;
  endTime: number;
}

const sdk = new SyncVestingProofSDK(MERKLE_PROOFS);

export function useClaimVesting(): QueryObserverResult<ClaimVesting | undefined> {
  const { library, account } = useEthers();

  return useQuery<ClaimVesting | undefined>(
    [],
    async () => {
      if (!library || !account) throw new Error('Impossible error');

      const record = sdk.getByAddress(account);
      if (!record || BigNumber.from(record.amount).eq(0)) return;

      const airdrop = AirDropMerkleProof__factory.connect(AIRDROP_CONTRACT_ADDRESS, library.getSigner());
      const merkleRoot = sdk.getByNodeId(1)!.digest;
      const [claimable, claimedAmount] = await airdrop.claimable(merkleRoot, account, record.startTime, record.endTime);
      return {
        startTime: record.startTime * 1000,
        endTime: record.endTime * 1000,
        // TODO read from contract
        tokenSymbol: AIRDROP_TOKEN_SYMBOL,

        claimable,
        claimedAmount,
        claimableAmount: BigNumber.from(record.amount),
        claim: async () => {
          const tx = await airdrop.claim(
            record.amount,
            record.startTime,
            record.endTime,
            sdk.merkleProofFromJson(account),
          );
          await tx.wait();
          return tx.hash;
        },
      };
    },
    { enabled: !!library && !!account, refetchInterval: 3000 },
  );
}
