import React, { useMemo } from 'react';
import styled from 'styled-components';
import { ConnectButton } from 'components/ConnectButton';
import { useEthers } from '@usedapp/core';
import MetaMaskLogo from './metamask-fox.svg';
import { Button } from 'components/Button';
import { useClaimVesting } from 'hooks/useClaimVesting';
import { formatUnits } from '@ethersproject/units';
import { useMutation } from 'react-query';
import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';

dayjs.extend(utc);

const VestingWrapper = styled.div`
  h2 {
    margin: 2rem 0;
  }

  h3 {
    margin: 1.5rem 0;
  }

  h5 {
    margin: 1rem 0;
  }

  .tip {
    color: #f444ba;
  }

  button {
    font-size: 1.2rem;
  }
`;

const Welcome: React.FC = () => {
  return (
    <div>
      <h2>WELCOME</h2>
      <h3>Please connect your wallet to claim your tokens</h3>

      <ConnectButton />
    </div>
  );
};

const Claim: React.FC = () => {
  const { data: vesting, isLoading } = useClaimVesting();

  const { mutate: claim, isLoading: isClaiming } = useMutation(() => {
    if (!vesting) throw new Error('You have no claimable amount');
    return vesting.claim();
  });

  const tips = useMemo(() => {
    if (isLoading) {
      return 'Data is loading...';
    }
    if (isClaiming) {
      return 'Claiming...';
    }
    if (!vesting) {
      return "Sorry, you don't have allocation. Please buy KBD tokens from the exchange";
    }

    if (vesting.claimedAmount.gt(0)) {
      return `You have claimed ${formatUnits(vesting.claimableAmount)} ${vesting.tokenSymbol}`;
    }

    if (!vesting.claimable) {
      return `The token claim has not started`;
    }

    // if (!vesting.claimable && dayjs(Date.now()).isAfter(vesting.endTime)) {
    //   return 'The token claim is ended';
    // }

    return `You have ${formatUnits(vesting.claimableAmount)} ${vesting.tokenSymbol} allocation`;
  }, [isClaiming, isLoading, vesting]);

  const claimablePeriod = useMemo(() => {
    if (!vesting || vesting.claimedAmount.gt(0) || !vesting.claimable) return null;
    const start = dayjs(vesting.startTime).utc().format('DD-MM-YYYY HH:mm:ss');
    return `Please claim after(UTC) ${start}`;
  }, [vesting]);

  const notClaimed = !(vesting?.claimedAmount.eq(0) && vesting?.claimableAmount.gt(0));

  return (
    <div>
      <h3 className="tip">{tips}</h3>
      {claimablePeriod && <h5 className="tip">{claimablePeriod}</h5>}
      <Button disabled={notClaimed} onClick={() => claim()}>
        Claim
      </Button>
    </div>
  );
};

export const Vesting: React.FC = () => {
  const { account } = useEthers();

  return (
    <VestingWrapper>
      <div style={{ maxWidth: '300px', margin: 'auto' }}>
        <img src={MetaMaskLogo} alt="connection" />
      </div>
      {account ? <Claim /> : <Welcome />}
    </VestingWrapper>
  );
};
